@use "../abstracts/variables";
@use "../abstracts/mixins";
@use "../theme/theme";

.main-product {
  @include mixins.boxFlex(initial, initial, column);

  @include mixins.media("tablet-l") {
    flex-direction: row;
  }
}

.main-product__media-wrapper {
  position: relative;
  width: 100%;

  @include mixins.media("tablet-l") {
    padding-left: 50px;
    padding-right: 10px;
    padding-top: 20px;
    width: 60%;
  }

  @include mixins.mediaCustom("min-width: 1100px") {
    padding-right: 30px;
  }
}

.main-product__slider {
  width: 100%;

  @include mixins.media("tablet-l") {
    position: sticky;
    top: 94px;
  }
}

.main-product__slider-principal-wrapper {
  position: relative;
  width: 100%;
  height: 100%;

  @include mixins.media("tablet-l") {
    width: 73%;
  }

  /*
  
  @include mixins.media("desktop") {
    width: calc(100% - 180px);
  }

  @include mixins.mediaCustom("min-width: 1350px") {
    width: calc(100% - 220px);
  }

  @include mixins.media("desktop-m") {
    width: calc(100% - 230px);
  }

  @include mixins.mediaCustom("min-width: 1620px") {
    width: calc(100% - 270px);
  }

  @include mixins.media("desktop-l") {
    width: calc(100% - 300px);
  }

  @include mixins.media("desktop-xl") {
    width: calc(100% - 340px);
  }
  */

  .wk-button {
    position: absolute;
    right: 0px;
    top: 0px;
    padding: 20px;

    @include mixins.media("tablet-l") {
      padding: 20px;
    }

    .wk-button__icon {
      width: 24px;
      height: 24px;
      margin-right: 0;
    }

    .wk-button__label {
      display: none;
    }

    svg {
      height: 24px;
      width: 24px;
    }

    svg path {
      stroke-width: 2px;
    }
  }

  .swiper-pagination {
    margin-top: 20px;
    flex-wrap: wrap;
    padding: 0 60px;

    @include mixins.media("tablet-l") {
      display: none;
    }
  }

  .product-card__status-tag-wrapper {
    top: 22px;
  }

  .product-card__status-tag {
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-s;
  }
}

.main-product__slider-thumbs-wrapper {
  width: 100%;
  @include mixins.boxFlex(initial, center, column);
}

.main-product__slider-principal.swiper {
  position: relative;
  z-index: 0;
  height: 100%;

  @include mixins.media("tablet") {
    height: initial;
    margin-left: 50px;
    margin-right: 50px;
  }

  @include mixins.media("tablet-l") {
    margin-left: 10px;
    margin-right: 0;
  }

  .swiper-slide {
    height: 0;
    position: relative;
    padding-bottom: 100%;
  }

  .swiper-slide img {
    position: absolute;
    height: 100%;
    width: 100%;
  }
}

.main-product__slider-thumbs,
.swiper-button--product-thumbs {
  display: none;

  @include mixins.media("tablet-l") {
    display: block;
  }
}

.swiper-button--product-thumbs {
  svg {
    @include mixins.clickEffect;
  }
}

.main-product__slider-view {
  //margin-top: 25px;
  @include mixins.boxFlex;

  @include mixins.media("tablet-l") {
    margin-bottom: 0;
  }

  @media screen and (max-width: 992px) {
    margin-top: 25px;
  }

  button:not(:first-child) {
    margin-left: 60px;
  }

  button {
    @include mixins.clickEffect;

    svg {
      display: block;
      width: 30px;
    }

    svg.icon-360 {
      width: 37px;
    }
  }

  .main-product__zoom-icon {
    @include mixins.media("tablet-l") {
      display: none;
    }
  }
}

.main-product__slider-view--img-modal {
  top: -100%;
  position: fixed;
  z-index: 20;
  left: 50%;
  width: 90%;
  max-width: 362px;
  transform: translate(-50%, -100%);
  color: variables.$color-background-2;
  font-weight: 400;
  transition: all 0.4s ease-in;
  @include mixins.boxFlex;

  @include mixins.media("tablet-l") {
    max-width: 500px;
    max-height: 500px;
  }

  &[data-active="true"] {
    top: 50%;
    transform: translate(-50%, -50%);

    .main-product__slider-view--img {
      position: absolute;
      display: block;
      height: auto;
      margin: 0 auto;
      width: auto;
    }
  }

  &[data-active="false"] {
    top: -100%;
    transform: translate(-50%, -100%);
  }
}

.main-product__slider-thumbs {
  //margin: 15px 0;
  width: 100%;

  .swiper-wrapper {
    @include mixins.media("tablet-l") {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      justify-items: center;
    }
  }

  // .swiper-wrapper {
  //   @include mixins.media("tablet-l") {
  //     display: grid;
  //     grid-template-columns: repeat(2, 1fr);
  //   }
  // }

  img {
    height: 100%;
    width: 100%;
    padding: 2px;
  }

  .image-elements__image-thumbs {
    //max-width: 80px;
    padding-bottom: 86%;
    width: 95%;
    cursor: pointer;
    position: relative;
    height: fit-content !important;

    img {
      position: absolute;
    }

    /*
    @include mixins.media("tablet-l") {
      max-width: 80px;
    }

    @include mixins.media("desktop") {
      max-width: 90px;
    }

    @include mixins.mediaCustom("min-width: 1350px") {
      max-width: 100px;
    }

    @include mixins.media("desktop-m") {
      max-width: 110px;
    }

    @include mixins.mediaCustom("min-width: 1620px") {
      max-width: 135px;
    }

    @include mixins.media("desktop-l") {
      max-width: 150px;
    }

    @include mixins.media("desktop-xl") {
      max-width: 170px;
    }
    
     */
  }

  .swiper-slide-thumb-active img {
    border: 1px solid var(--color-base-principal);
  }
}

.product-image-modal {
  & > div {
    background-color: variables.$color-white;
    height: 70%;
    width: 95%;
    overflow: auto;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  img {
    height: 100%;
    width: 100%;
    padding: 0;
    transform-box: fill-box;
    object-fit: cover;
    object-position: center;
  }
}

.pswp {
  .pswp__button:not(.pswp__button--close) {
    display: none;
  }

  .pswp__button--close {
    width: 40px;

    use {
      stroke: none;
      width: 1px;
    }

    .pswp__icn {
      height: 40px;
      width: 28px;
    }
  }
}

.main-product__detail {
  margin-top: 20px;
  padding-bottom: 13px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 13px;
  background-color: variables.$color-secondary;
  width: 100%;

  @include mixins.media("tablet-l") {
    margin-top: 0;
    padding-left: 35px;
    padding-right: 50px;
    padding-top: 30px;
    width: 40%;
  }

  .main-product__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 14px;

    @include mixins.media("tablet-l") {
      align-items: flex-start;
      margin-bottom: 30px;
    }
  }

  h1 {
    margin: 0;
    font-weight: 300;
    text-align: center;
    @extend .h3;

    @include mixins.media("tablet-l") {
      text-align: left;
    }
  }

  .product.price {
    margin-bottom: 14px;
    text-align: center;

    @include mixins.media("tablet-l") {
      margin-bottom: 30px;
      text-align: left;
    }

    .regular {
      font-weight: 400;
    }

    .compare {
      color: variables.$color-background-2;
      margin-right: 8px;
    }

    .compare,
    .regular {
      font-size: variables.$font-size--general-l;
      line-height: variables.$line-height--general-l;
    }

    .compare + .regular {
      color: variables.$color-accent-3;
    }
  }

  #alma-badge {
    margin-bottom: 14px;

    @include mixins.media("tablet") {
      grid-template-columns: 1fr;
      max-width: 330px;
      margin: 0 auto 14px;
    }
    @include mixins.media("desktop") {
      margin: inherit;
      margin-bottom: 12px;
      max-width: 100%;
      @include mixins.boxGrid(repeat(2, 1fr), unset, 14px, unset);
    }

    .alma-payment-plans-container {
      gap: 0;
      padding-bottom: 12px;
      width: 100%;
      background-color: transparent;
      border: 1px solid variables.$color-background;
      align-items: center;
      @include mixins.media("desktop") {
        align-items: flex-start;
      }
    }
  }

  .main-product__form {
    margin-bottom: 22px;

    button {
      width: 100%;
      min-height: 43px;
    }

    form:nth-child(2) {
      margin-top: 14px;

      @include mixins.mediaCustom("min-width: 400px") {
        margin-top: 0;
      }
    }

    .btn-2.btn-checkout,
    .btn-2.btn-checkout:hover {
      #loading,
      .loading {
        border-top-color: variables.$color-principal;
      }
    }
  }

  .main-product__form-variants-label {
    span {
      display: block;
      margin-bottom: 9px;
      font-size: variables.$font-size--general-up-m;
      line-height: variables.$line-height--general-m;
      text-align: center;

      @include mixins.media("tablet-l") {
        text-align: left;
      }
    }
  }

  .variants {
    margin-bottom: 11px;

    @include mixins.media("tablet-l") {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 14px;
    }

    .variants--name {
      display: none;
    }

    .variants--select-tag {
      border: 1px solid variables.$color-background;
      padding: 8px 12px;
      background-color: variables.$color-white;
      color: variables.$color-background-2;
      background-size: 15px;
      background-image: var(--icon-caret-blue);
    }
  }

  .main-product__form-wrapper {
    @include mixins.boxGrid(1fr, repeat(2, 1fr), unset, 14px);

    @include mixins.mediaCustom("min-width: 768px") {
      @include mixins.boxGrid(repeat(2, 1fr), unset, 14px, unset);
    }
  }

  .certified-open {
    margin-top: 8px;
    @include mixins.boxFlex(initial);

    img {
      height: 30px;
      width: 30px;
      margin-right: 5px;
      @include mixins.clickEffect;
    }

    span {
      font-family: variables.$font-family--title;
      font-size: variables.$font-size--general-l;
      font-weight: 400;
      line-height: variables.$line-height--general-l;
      color: variables.$color-black;
      @include mixins.linkEffect(currentColor, 1px);
      @include mixins.clickEffect;
    }

    span::after {
      bottom: 4;
    }
  }
}

.main-product .product-card__brand {
  display: block;
  text-transform: uppercase;
  color: variables.$color-black;
  text-align: center;
  margin-bottom: 8px;
  @extend .h5;

  @include mixins.media("tablet-l") {
    margin-bottom: 0;
    text-align: left;
  }
}

.main-product .main-product__share-btn span,
.main-product .text__description {
  font-size: variables.$font-size--general-up-s;
  line-height: variables.$line-height--general-up-s;
  text-transform: uppercase;
  color: variables.$color-black;
}

.main-product .main-product__share-btn {
  margin-bottom: 22px;
  @include mixins.boxFlex(center);

  @include mixins.media("tablet-l") {
    justify-content: flex-start;
    margin-bottom: 28px;
  }

  svg {
    width: 12px;
    margin-right: 10px;
  }

  span {
    @include mixins.linkEffect(currentColor, 1px);
  }
}

.clipboard-message {
  bottom: 20px;
  position: fixed;
  left: 50%;
  z-index: 2;
  padding: 15px;
  transform: translateX(-50%);
  font-weight: 500;
  background-color: variables.$color-principal;
  color: variables.$color-white;
}

.main-product .icon-whith-text {
  margin-bottom: 28px;
  flex-wrap: wrap;
  @include mixins.boxFlex(center, flex-start);

  @include mixins.mediaCustom("min-width: 350px") {
    justify-content: space-around;
  }

  @include mixins.mediaCustom("min-width: 350px") {
    flex-wrap: nowrap;
  }

  @include mixins.media("tablet-l") {
    margin-bottom: 35px;
  }

  .text__with-icon {
    @include mixins.boxFlex;

    @include mixins.mediaCustom("min-width: 350px") {
      justify-content: space-around;
    }
  }

  .text__with-icon {
    align-items: flex-start;
    border: unset;
    margin: 0;
    max-width: 90px;
    padding: 0;

    @include mixins.media("tablet") {
      max-width: 137px;
    }

    &:nth-child(3) {
      margin-top: 10px;

      @include mixins.mediaCustom("min-width: 350px") {
        margin-top: 0;
      }
    }
  }

  .text__description {
    @include mixins.mediaCustom("max-width: 991px") {
      font-size: variables.$font-size--general-xs;
      line-height: variables.$line-height--general-xs;
    }
  }

  img {
    height: 36px;
    width: 36px;
  }
}

.main-product__description {
  * {
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-s;
  }

  &.carte-cardeu {
    @include mixins.mediaCustom("max-width: 991px") {
      text-align: center;
    }
    * {
      font-size: variables.$font-size--general-m;
    }
  }

  a {
    color: variables.$color-text;
    @include mixins.linkEffect(currentColor, 1px);
  }
}

.main-product__accordion {
  .icon-incognito {
    height: 12px;
    width: 12px;
    @include mixins.clickEffect;
  }

  .accordion-item__content,
  .accordion-item__button {
    padding: 0;
  }

  .accordion-item__content {
    color: variables.$color-black;
  }

  .accordion-item__button {
    padding-bottom: 13px;
    border-bottom: 1px solid variables.$color-background;

    span {
      color: variables.$color-principal;
      font-family: variables.$font-family--title;
      font-size: variables.$font-size--general-up-l;
      line-height: variables.$line-height--general-m;
    }

    svg {
      width: 11px;
      height: 11px;
      transition: transform 0.3s;
    }
  }

  .accordion-item__button[data-active="true"] {
    svg {
      transform: rotate(180deg);
    }

    & ~ div.accordion-item__content {
      max-height: initial;
      padding-bottom: 12px;
      padding-left: 0;
      padding-right: 0;
      padding-top: 12px;
    }
  }

  ul li {
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-up-s;
  }

  ul:not(.with-bullet) {
    padding-left: 0;
    list-style: none;
  }

  ul.with-bullet {
    list-style-position: inside;
    padding-left: 6px;

    li::marker {
      font-size: 8px;
    }
  }
}

.main-product__accordion-description {
  .accordion-item__content {
    @include mixins.boxGrid(repeat(2, 1fr), auto, max(50px), 20px);

    @include mixins.media("tablet") {
      column-gap: max(129px);
    }

    @include mixins.media("tablet-l") {
      column-gap: max(20px);
    }

    @include mixins.media("desktop") {
      column-gap: max(60px);
    }

    @include mixins.media("desktop-s") {
      column-gap: max(100px);
    }

    @include mixins.media("desktop-m") {
      column-gap: max(129px);
    }
  }
}

.descript-product-item {
}

.descript-product-item__title {
  margin-bottom: 3px;
  font-size: variables.$font-size--general-up-s;
  line-height: variables.$line-height--general-m;
  text-transform: uppercase;

  @include mixins.media("tablet") {
    font-size: variables.$font-size--general-up-m;
  }
}

.descript-product-item__sub-item {
  font-size: variables.$font-size--general-s;
  line-height: variables.$line-height--general-up-s;
}

.description-modal {
  display: none;
  position: fixed;
  z-index: 20;
  top: 50%;
  left: 50%;
  height: 88%;
  max-height: 90%;
  width: 96%;
  padding: 14px 16px;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  background-color: variables.$color-white;
  color: variables.$color-background-2;
  font-weight: 400;
  transition: all 0.4s ease-in;
  @include mixins.scroll(variables.$color-background-3, 5px, 5px, variables.$color-background-5);

  @include mixins.mediaCustom("min-width: 620px") {
    max-width: 582px;
  }

  @include mixins.media("tablet") {
    height: auto;
    padding: 20px 33px 33px 33px;
    width: 90%;
  }

  &[data-active="true"] {
    display: block;
  }

  .description-modal__close {
    display: block;
    margin-left: auto;
    line-height: 0;
    @include mixins.clickEffect;

    path {
      fill: variables.$color-black;
    }
  }

  .description-modal__title {
    display: block;
    margin-bottom: 5px;
    font-family: variables.$font-family--title;
    font-size: variables.$font-size--mobile-h4;
    line-height: variables.$line-height--mobile-h4;
    color: variables.$color-principal;
    text-align: center;
    text-transform: uppercase;
  }

  .description-modal__description {
    margin-bottom: 17px;
    padding: 12px;
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-up-s;
    background-color: variables.$color-secondary;
    @include mixins.boxFlex(initial);

    @include mixins.media("tablet") {
      padding: 12px 31px;
    }

    &-icon {
      height: 36px;
      width: 36px;
    }

    &-text {
      font-weight: 300;
      color: variables.$color-black;
      margin-left: variables.$margin-xs;

      @include mixins.media("tablet") {
        margin-left: variables.$margin-m;
      }
    }
  }

  .description-modal__grid {
    @include mixins.boxGrid(repeat(2, 1fr), auto, 12px, 12px);

    @include mixins.media("tablet") {
      column-gap: 15px;
      row-gap: 17px;
    }
  }

  .description-modal__grid-item {
    border: 1px solid variables.$color-principal;
    padding: 11px;

    @include mixins.media("tablet") {
      padding: 11px 20px 20px 20px;
    }

    & > span {
      display: block;
      margin-bottom: 3px;
      font-family: variables.$font-family--title;
      font-size: variables.$font-size--mobile-h6;
      line-height: variables.$line-height--mobile-h6;
      color: variables.$color-principal;
    }

    & > div {
      font-size: variables.$font-size--general-s;
      line-height: variables.$line-height--general-up-s;
      color: variables.$color-background-2;
    }
  }

  .description-modal__grid.modal-shoulder-strap {
    .description-modal__grid-item > span {
      text-align: center;
      margin-bottom: 8px;

      @include mixins.media("tablet") {
        margin-bottom: 16px;
      }
    }

    .description-modal__grid-item {
      padding: 6px;

      @include mixins.media("tablet") {
        padding: 4px;
      }
    }

    .description-modal__grid-item img {
      display: block;
      height: 100%;
      margin: 0 auto;
      max-height: 145px;
      max-width: 218px;
      width: 100%;
      object-fit: contain;
    }
  }
}

/* SLIDER VERTICAL LEFT OR RIGHT OUTSIDE IMAGE */
.vertical-outside-swipper {
  display: flex;

  &.vertical-left {
    flex-direction: row-reverse;
  }

  .main-product__slider-thumbs-wrapper {
    padding-bottom: 72%;
    position: relative;
    height: 100%;
    margin-left: -1.4%;
    & > div {
      position: absolute;
    }

    .main-product__slider-thumbs {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto 0;
      height: 90%;
      z-index: 0;

      &.center-slider {
        display: flex;
        align-items: center;
      }
    }

    .swiper-button {
      &-prev--main-product {
        top: -20px;
      }
      &-next--main-product {
        bottom: -20px;
      }
    }
  }
}

.swiper-button--product-thumbs {
  height: 40px;
  width: 40px;
  z-index: 1;

  svg {
    display: block;
    height: 40px;
    width: 40px;
    margin: 0 auto;
    cursor: pointer;
  }

  &.swiper-button-next--main-product svg {
    transform: rotateZ(90deg) translateX(-6px);
  }

  &.swiper-button-prev--main-product svg {
    transform: rotateZ(-90deg) translateX(-6px);
  }

  &.swiper-button-disabled {
    opacity: 0.3;
  }
}

.main-product__detail.main-product--out-of-stock {
  .main-product__form-wrapper {
    grid-template-columns: 1fr;
  }

  .btn-add-to-cart {
    color: variables.$color-white;

    &:hover {
      background-color: variables.$color-principal;
    }
  }
}

.main-product__media-wrapper.main-product--out-of-stock {
  .main-product__slider-thumbs-wrapper,
  .main-product__slider-principal {
    opacity: 0.5;
  }
}

/* VARIANTS STICKY*/

.sticky-cart {
  @include mixins.mediaCustom("max-width: 749px") {
    background-color: #fff;
    bottom: 0;
    box-shadow: 2.72076px -8px 24px rgb(0 0 0 / 0.15);
    box-sizing: border-box;
    height: auto;
    left: 0;
    padding: 0.6rem;
    position: fixed;
    transition: bottom 0.5s ease-in-out;
    width: 100%;
    z-index: 101;
    display: none;
  }
}

.sticky-cart {
  display: none;
}

.img-magnifier-container {
  position: relative;
}

.img-magnifier-glass {
  position: absolute;
  border-radius: 50%;
  overflow: hidden;
  filter: drop-shadow(0 0 2px variables.$color-black);
  cursor: none;
  width: 200px;
  height: 200px;
  z-index: 1;
}

.modal-container {
  display: none;
}

.img-magnifier-glass {
  @include mixins.mediaCustom("max-width: 991px") {
    visibility: hidden;
  }
}

.modal-container {
  @include mixins.mediaCustom("max-width: 991px") {
    position: fixed;
    z-index: 20;
    top: 50%;
    left: 50%;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    font-weight: 400;
    background-color: rgba(0, 0, 0, 0.5);
    overflow: hidden;
    @include mixins.boxFlex;
  }
}

.slider-section__title {
  color: variables.$color-black !important;
}
