@use "../abstracts/variables";
@use "../abstracts/mixins";

.photo-plus__open {
  margin: 24px auto 36px;
  @include mixins.boxFlex(initial, center);
  @include mixins.clickEffect;

  @include mixins.media('tablet-l') {
    margin: 30px 0 40px;
  }

  img.icon-camera {
    height: 20px;
    width: 20px;
    margin-right: 8px;
  }

  span {
    font-size: variables.$font-size--general-m;
    font-weight: 400;
    line-height: variables.$line-height--general-m;
    color: variables.$color-principal;
    @include mixins.linkEffect(currentColor, 1px);
  }

  span::after {
    bottom: 4px;
  }
}

.plus-photo-modal {
  padding: 14px 16px 24px;
  height: auto;

  @include mixins.mediaCustom("min-width: 620px") {
    max-width: 436px;
  }

  @include mixins.media("tablet") {
    padding: 20px 24px 48px;
  }

  .description-modal__close {
    @include mixins.media("tablet") {
      padding-bottom: 17px;
    }
  }

  .description-modal__title {
    margin-bottom: 8px;
    font-weight: 300;
  }

  .description-modal__description {
    justify-content: center;
    margin-bottom: 24px;
    padding: 0;
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
    background-color: unset;
  }

  .description-modal__description-text {
    margin-left: 0;
    text-align: center;
  }

  .form__textfield-label > label,
  .form__checkbox-label {
    color: variables.$color-black;
  }

  .form__textfield-label > label {
    margin-bottom: 4px;
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
  }

  .form__textfield-label--spaced {
    margin-bottom: 1rem;

    &.form__checkbox  {
      margin-bottom: 24px;
    }
  }

  .form__textfield-label.size-s input {
    padding: 9px  10px;
    color: variables.$color-black;
  }

  .form__textfield-label.size-s {
    input[type='tel'] {
      padding: 11px 0;
      padding-left: 52px;
    }
  }

  input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 40rem variables.$color-white inset;
  }

  .btn-1 {
    width: 100%;

    .loading {
      border-top-color: variables.$color-white;
    }

    &:hover .loading {
      border-top-color: variables.$color-principal;
    }
  }

}

.photo-plus__response-error {
  display: block;
  text-align: center;
  font-size: variables.$font-size--mobile-h6;
  line-height: variables.$line-height--mobile-h6;
  color: variables.$color-principal;
}

.photo-plus__response-error {
  margin-top: 24px;
  color: variables.$color-error;
}

.form__checkbox-label  {
  width: calc(100% - 20px);
  font-size: variables.$font-size--general-s;
  line-height: variables.$line-height--general-up-s;
}

#js-klaviyo-plus-photo {
  display: none;
}
